	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"CSS tinkering: font size","date":"18 Oct 2004","slug":"/2004-10-18-css-tinkering-font-size/","tags":"Y2004"},"html":"<p><a title=\"Webpropaganda: CSS basteln: Schriftgrösse\" href=\"http://www.couchblog.de/webpropaganda/development/527/css-basteln-schriftgrsse\">Webpropaganda: CSS tinkering: font size</a> 62.5%, the magic number. Finally ems make sense. Immediately inserted here.</p>"},"next":{"fields":{"title":"Sticker Shock","date":"17 Oct 2004","slug":"/2004-10-17-sticker-shock/"}},"previous":{"fields":{"title":"Spampot","date":"18 Oct 2004","slug":"/2004-10-18-spampot/"}}},{"node":{"fields":{"title":"Sticker Shock","date":"17 Oct 2004","slug":"/2004-10-17-sticker-shock/","tags":"Y2004"},"html":"<p><a href=\"http://www.haughty.us/blog/archives/2004/10/was_that_a_comp.html\" title=\"Haughty Bill's - View of Life: Was That......Ummmm..... A Compliment? ;-)\">Haughty Bill:</a> \"Yesterday I stopped into one of the local liquor stores to buy a bottle of Merlot wine. My favorite. OMG! Sticker shock! They want $10.50 for my $5 bottle of wine in Germany.\" Not everything is <a href=\"http://www.haughty.us/blog/archives/2004/10/a_case_of_admis.html\" title=\"Haughty Bill's - View of Life: A Case of Admission!\">better</a> in the States...</p>"},"next":{"fields":{"title":"Conquerors or Liberators Revisited","date":"17 Oct 2004","slug":"/2004-10-17-conquerors-or-liberators-revisited/"}},"previous":{"fields":{"title":"CSS tinkering: font size","date":"18 Oct 2004","slug":"/2004-10-18-css-tinkering-font-size/"}}},{"node":{"fields":{"title":"Conquerors or Liberators Revisited","date":"17 Oct 2004","slug":"/2004-10-17-conquerors-or-liberators-revisited/","tags":"Y2004"},"html":"<p><a href=\"http://www.papascott.de/archives/2003/08/25/conquerors-or-liberators/\" title=\"PapaScott: Conquerors or Liberators\">Allow me to repeat myself:</a>  \"Even in April (2003) it was clear that an troops would be in Iraq for at least two years, well past the 2004 election. And I also remember thinking back then that, from his timing of the war, President Bush was handing the fate of his re-election away from US voters and to the Iraqi people. If, say, in the summer of 2004 the occupation is going badly, the Iraqis do not feel liberated and 'better off than they were 4 years ago', if US soldiers are still killing and being killed, Iraq would then dominate the US election and Bush would be like LBJ in 1968, namely toast.... Or, as <a title=\"BuzzMachine: Bye bye Bush?\" href=\"http://www.buzzmachine.com/archives/2003_08.html#004514\">Buzz Machine</a> commented: 'The war was a success. But the peace is hell.'\"</p>\n<p>It looks like I was wrong about LBJ and toast, but the peace has turned out to be more hellish that I ever imagined.</p>"},"next":{"fields":{"title":"Lantern March","date":"16 Oct 2004","slug":"/2004-10-16-lantern-march/"}},"previous":{"fields":{"title":"Sticker Shock","date":"17 Oct 2004","slug":"/2004-10-17-sticker-shock/"}}}],"pathPrefix":"","first":false,"last":false,"index":401,"pageCount":1101}}

